import React, { useState, useEffect } from 'react';
import './AgentInteractionFlow.css';

const AgentInteractionFlow = ({ events, selectedEvent }) => {
  const [interactionFlow, setInteractionFlow] = useState([]);

  // 
  const agentConfig = {
    'master_agent': { name: '', icon: '', color: '#ff6b6b' },
    'traffic_manager': { name: '', icon: '', color: '#4ecdc4' },
    'safety_agent': { name: '', icon: '', color: '#45b7d1' },
    'weather_agent': { name: '', icon: '', color: '#96ceb4' },
    'parking_agent': { name: '', icon: '', color: '#feca57' },
    'autonomous_driving': { name: '', icon: '', color: '#ff9ff3' }
  };

  // 
  useEffect(() => {
    if (!selectedEvent || !events) return;

    const relatedEvents = getRelatedEvents(selectedEvent, events);
    const flow = generateInteractionFlow(selectedEvent, relatedEvents);
    setInteractionFlow(flow);
  }, [selectedEvent, events]);

  const getRelatedEvents = (sentEvent, allEvents) => {
    if (!sentEvent || !sentEvent.timestamp) return [];
    
    const timeWindow = 10 * 60 * 1000; // 10
    const sentTime = new Date(sentEvent.timestamp);
    
    return allEvents.filter(event => {
      if (!event.timestamp || event.id === sentEvent.id) return false;
      
      const eventTime = new Date(event.timestamp);
      const timeDiff = Math.abs(eventTime.getTime() - sentTime.getTime());
      
      return timeDiff <= timeWindow;
    }).sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp));
  };

  const generateInteractionFlow = (sentEvent, relatedEvents) => {
    const flow = [];
    
    // 1. 
    const eventType = sentEvent?.type || sentEvent?.event_type || '';
    const eventTitle = getEventTitle(sentEvent);
    
    flow.push({
      id: 'initial',
      type: 'event',
      agent: getAgentFromEvent(sentEvent),
      message: `: ${eventTitle}`,
      timestamp: sentEvent?.timestamp,
      status: 'completed'
    });

    // 2. 
    relatedEvents.forEach(event => {
      const agent = getAgentFromEvent(event);
      const message = getEventMessage(event);
      
      flow.push({
        id: event.id || `event_${Date.now()}`,
        type: event.type,
        agent: agent,
        message: message,
        timestamp: event.timestamp,
        status: 'completed',
        details: event.payload
      });
    });

    return flow;
  };

  const getEventTitle = (event) => {
    if (!event) return '';
    
    const eventType = event.type || event.event_type || '';
    const payload = event.payload || {};
    
    switch (eventType) {
      case 'weather_alert':
        return ` - ${payload.alert_type || ''}`;
      case 'autonomous_driving':
        return ` - ${payload.start_location || ''}  ${payload.end_location || ''}`;
      case 'parking_update':
        return ` - ${payload.location || ''}`;
      case 'safety_inspection':
        return ` - ${payload.location || ''}`;
      default:
        return eventType || '';
    }
  };

  const getAgentFromEvent = (event) => {
    if (!event) return 'master_agent';
    
    const eventType = event.type || event.event_type || '';
    const payload = event.payload || {};
    
    switch (eventType) {
      case 'weather_alert':
        return 'weather_agent';
      case 'autonomous_driving':
        return 'autonomous_driving';
      case 'parking_update':
        return 'parking_agent';
      case 'safety_inspection':
        return 'safety_agent';
      case 'workflow_start':
      case 'workflow_complete':
        return 'master_agent';
      case 'main_task_result':
        return 'master_agent';
      case 'coordination_start':
      case 'coordination_result':
        return 'master_agent';
      case 'agent_response':
        // payload
        if (typeof payload === 'object' && payload !== null && payload.agent) {
          const agentName = payload.agent;
          if (typeof agentName === 'string') {
            if (agentName.includes('')) return 'traffic_manager';
            if (agentName.includes('')) return 'safety_agent';
            if (agentName.includes('')) return 'weather_agent';
            if (agentName.includes('')) return 'parking_agent';
            if (agentName.includes('')) return 'autonomous_driving';
          }
        }
        return 'master_agent';
      case 'agent_error':
        return 'master_agent';
      default:
        // payload
        if (typeof payload === 'string' && payload.length > 0) {
          if (payload.includes('TrafficManager') || payload.includes('')) return 'traffic_manager';
          if (payload.includes('SafetyAgent') || payload.includes('')) return 'safety_agent';
          if (payload.includes('WeatherAgent') || payload.includes('')) return 'weather_agent';
          if (payload.includes('ParkingAgent') || payload.includes('')) return 'parking_agent';
          if (payload.includes('AutonomousDriving') || payload.includes('')) return 'autonomous_driving';
        } else if (typeof payload === 'object' && payload !== null) {
          // 
          const payloadStr = JSON.stringify(payload);
          if (payloadStr.includes('TrafficManager') || payloadStr.includes('')) return 'traffic_manager';
          if (payloadStr.includes('SafetyAgent') || payloadStr.includes('')) return 'safety_agent';
          if (payloadStr.includes('WeatherAgent') || payloadStr.includes('')) return 'weather_agent';
          if (payloadStr.includes('ParkingAgent') || payloadStr.includes('')) return 'parking_agent';
          if (payloadStr.includes('AutonomousDriving') || payloadStr.includes('')) return 'autonomous_driving';
        }
        
        return 'master_agent';
    }
  };

  const getEventMessage = (event) => {
    const eventType = event.type || '';
    const payload = event.payload || {};
    
    switch (eventType) {
      case 'workflow_start':
        return '';
      case 'main_task_result':
        return '';
      case 'coordination_start':
        return '';
      case 'coordination_result':
        return '';
      case 'workflow_complete':
        return '';
      case 'agent_response':
        // payload
        if (typeof payload === 'object' && payload !== null && payload.agent) {
          return `${payload.agent}`;
        }
        return '';
      case 'agent_error':
        return '';
      default:
        if (typeof payload === 'string' && payload.length > 0) {
          if (payload.includes('') || payload.includes('heavy_rain')) {
            if (payload.includes('TrafficManager')) return '';
            if (payload.includes('SafetyAgent')) return '';
          }
        } else if (typeof payload === 'object' && payload !== null) {
          const payloadStr = JSON.stringify(payload);
          if (payloadStr.includes('') || payloadStr.includes('heavy_rain')) {
            if (payloadStr.includes('TrafficManager')) return '';
            if (payloadStr.includes('SafetyAgent')) return '';
          }
        }
        return '';
    }
  };

  if (!selectedEvent || interactionFlow.length === 0) {
    return (
      <div className="agent-interaction-flow">
        <div className="flow-placeholder">
          <div className="placeholder-icon"></div>
          <div className="placeholder-text"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="agent-interaction-flow">
      <div className="flow-header">
        <h3> </h3>
        <div className="flow-info">
           {interactionFlow.length} 
        </div>
      </div>
      
      <div className="flow-timeline">
        {interactionFlow.map((step, index) => {
          const agent = agentConfig[step.agent] || { name: '', icon: '', color: '#ccc' };
          
          return (
            <div key={step.id} className="timeline-step completed">
              <div className="step-indicator" style={{ backgroundColor: agent.color }}>
                <div className="step-icon">{agent.icon}</div>
              </div>
              <div className="step-content">
                <div className="step-title">{step.message}</div>
                <div className="step-agent">{agent.name}</div>
                <div className="step-time">
                  {step.timestamp ? new Date(step.timestamp).toLocaleTimeString() : ''}
                </div>
                {step.details && (
                  <div className="step-details">
                    {typeof step.details === 'string' ? step.details.substring(0, 100) + '...' : ''}
                  </div>
                )}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default AgentInteractionFlow;
