// DeepSeek API 
export const DEEPSEEK_CONFIG = {
  // DeepSeek API 
  API_URL: 'https://api.deepseek.com/v1/chat/completions',
  
  // API  ()
  API_KEY: process.env.REACT_APP_DEEPSEEK_API_KEY || 'your-deepseek-api-key-here',
  
  // 
  MODEL: 'deepseek-chat',
  
  // 
  REQUEST_CONFIG: {
    temperature: 0.7,
    max_tokens: 1000,
    top_p: 0.9,
    frequency_penalty: 0,
    presence_penalty: 0
  }
};

// 
export const SYSTEM_PROMPT = `Multi-Agent DSL PlatformAIDSL


1. 
2. DSLEVENT_ROUTELLM_DRIVESYSTEM_SCHEDULEAGENT_COLLABORATECACHE_OPTIMIZE
3. 
4. 

`;

// DeepSeek API 
export const callDeepSeekAPI = async (userMessage, conversationHistory = []) => {
  try {
    const messages = [
      { role: 'system', content: SYSTEM_PROMPT },
      ...conversationHistory,
      { role: 'user', content: userMessage }
    ];

    const response = await fetch(DEEPSEEK_CONFIG.API_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${DEEPSEEK_CONFIG.API_KEY}`
      },
      body: JSON.stringify({
        model: DEEPSEEK_CONFIG.MODEL,
        messages: messages,
        ...DEEPSEEK_CONFIG.REQUEST_CONFIG
      })
    });

    if (!response.ok) {
      throw new Error(`API: ${response.status} ${response.statusText}`);
    }

    const data = await response.json();
    
    if (data.choices && data.choices.length > 0) {
      return data.choices[0].message.content;
    } else {
      throw new Error('API');
    }
    
  } catch (error) {
    console.error('DeepSeek API:', error);
    throw error;
  }
};

// API
export const callDeepSeekAPIMock = async (userMessage) => {
  // 
  await new Promise(resolve => setTimeout(resolve, 1000 + Math.random() * 2000));
  
  // 
  const responses = {
    'dsl': "DSLEVENT_ROUTELLM_DRIVEAISYSTEM_SCHEDULEAGENT_COLLABORATECACHE_OPTIMIZE",
    '': "CPU",
    '': "CACHE_OPTIMIZE",
    '': "DSL",
    '': "DSLEVENT_ROUTESYSTEM_SCHEDULE"
  };
  
  // 
  const lowerMessage = userMessage.toLowerCase();
  for (const [keyword, response] of Object.entries(responses)) {
    if (lowerMessage.includes(keyword)) {
      return response;
    }
  }
  
  return responses[''];
};
