// frontend/src/components/InteractionHistory.jsx
import React, { useState } from 'react';
import './InteractionHistory.css';
import WebSocketService from './WebSocketService';
import AgentInteractionFlow from './AgentInteractionFlow';

const InteractionHistory = ({ 
  events = [], 
  onClear, 
  onGenerateReport, 
  onOpenReportSidebar, 
  showReportsOnly, 
  onToggleReportsOnly, 
  readyState, 
  connectionStats,
  connectionError,
  isRetrying,
  onRetryConnection
}) => {
  const [selectedSentEvent, setSelectedSentEvent] = useState(null);
  const [expandedEvents, setExpandedEvents] = useState(new Set());

  const getStatusPill = () => {
    if (isRetrying) {
      return <span className="pill pill-status-connecting">  / Retrying...</span>;
    }
    
    if (connectionError) {
      return (
        <div className="connection-error-container">
          <span className="pill pill-status-closed">  / Connection Error</span>
          <button 
            className="retry-button" 
            onClick={onRetryConnection}
            disabled={isRetrying}
          >
              / Retry
          </button>
        </div>
      );
    }
    
    switch (readyState) {
      case 0: // CONNECTING
        return <span className="pill pill-status-connecting">  / Connecting...</span>;
      case 1: // OPEN
        return <span className="pill pill-status-open">  / Connected</span>;
      case 2: // CLOSING
        return <span className="pill pill-status-closing">  / Disconnecting...</span>;
      case 3: // CLOSED
        return (
          <div className="connection-error-container">
            <span className="pill pill-status-closed">  / Service Unavailable</span>
            <button 
              className="retry-button" 
              onClick={onRetryConnection}
              disabled={isRetrying}
            >
                / Retry
            </button>
          </div>
        );
      default:
        return (
          <div className="connection-error-container">
            <span className="pill pill-status-closed">  / Service Unavailable</span>
            <button 
              className="retry-button" 
              onClick={onRetryConnection}
              disabled={isRetrying}
            >
                / Retry
            </button>
          </div>
        );
    }
  };

  const handleReconnect = () => {
    WebSocketService.reconnect();
  };

  // 
  const getEventSummary = (event) => {
    switch (event.type) {
      case 'main_task_result':
        return `${event.payload.agent}: ${event.payload.result.substring(0, 50)}...`;
      case 'coordination_result':
        return `: ${event.payload.total_agents}`;
      case 'workflow_complete':
        return '';
      case 'agent_response':
        return `${event.payload.agent}: ${event.payload.result.substring(0, 50)}...`;
      case 'agent_message':
        return event.payload.substring(0, 50) + '...';
      case 'analysis_report':
        return '';
      case 'simulation_log':
        return event.payload.details ? event.payload.details.substring(0, 50) + '...' : '';
      default:
        return event.title || event.type;
    }
  };

  const getEventIcon = (type) => {
    const icons = {
      'workflow_start': '',
      'main_task_result': '', // Master Agent 
      'coordination_start': '',
      'coordination_result': '',
      'workflow_complete': '',
      'agent_message': '',
      'agent_response': '',
      'analysis_report': '',
      'traffic_incident': '',
      'weather_alert': '',
      'parking_update': '',
      'safety_inspection': '',
      'autonomous_driving': '',
      'traffic_monitor': '',
      'simulation_log': '',
      'error': '',
      'ping': '',
      'broadcast': '',
      'main_coordination': '',
      'sub_agent_coordination': '',
      'sub_agent_processing': '',
      'sub_agent_completed': '',
      'result_summary': '',
      'default': ''
    };
    return icons[type] || icons['default'];
  };

  const getEventTypeLabel = (type) => {
    const labels = {
      'workflow_start': ' / Workflow Start',
      'main_task_result': ' / Master Agent Coordination',
      'coordination_start': ' / Sub Agent Coordination',
      'coordination_result': ' / Sub Agent Collaboration',
      'workflow_complete': ' / Workflow Complete',
      'agent_message': ' / Agent Message',
      'agent_response': ' / Agent Response',
      'analysis_report': ' / Analysis Report',
      'traffic_incident': ' / Traffic Incident',
      'weather_alert': ' / Weather Alert',
      'parking_update': ' / Parking Update',
      'safety_inspection': ' / Safety Inspection',
      'autonomous_driving': ' / Autonomous Driving',
      'traffic_monitor': ' / Traffic Monitor',
      'simulation_log': ' / Simulation Log',
      'error': ' / Error',
      'ping': ' / Ping',
      'broadcast': ' / Broadcast',
      'main_coordination': ' / Main Coordination',
      'sub_agent_coordination': ' / Sub Agent Coordination',
      'sub_agent_processing': ' / Sub Agent Processing',
      'sub_agent_completed': ' / Sub Agent Completed',
      'result_summary': ' / Result Summary',
      'default': ' / Unknown Event'
    };
    return labels[type] || labels['default'];
  };

  const renderEvent = (event, index) => {
    if (!event) {
      return <p>No event data</p>;
    }

    const { type, payload, title, timestamp } = event;
    const time = timestamp ? new Date(timestamp).toLocaleTimeString() : 'No timestamp';
    const icon = getEventIcon(type);
    const typeLabel = getEventTypeLabel(type);

    const eventCard = (
      <div className={`event-card event-${type}`} key={`${type}-${timestamp || index}-${index}`}>
        <div className="event-header">
          <div className="event-icon">{icon}</div>
          <div className="event-info">
            <h4 className="event-title">{title || typeLabel}</h4>
            <span className="event-time">{time}</span>
          </div>
          <div className="event-actions">
            <button 
              className="action-btn view-btn" 
              onClick={() => viewEventDetails(event)}
              title=" / View Details"
            >
              
            </button>
            {type === 'agent_response' && (
              <button 
                className="action-btn replay-btn" 
                onClick={() => replayEvent(event)}
                title=" / Replay Event"
              >
                
              </button>
            )}
            {type === 'error' && (
              <button 
                className="action-btn retry-btn" 
                onClick={() => retryEvent(event)}
                title=" / Retry"
              >
                
              </button>
            )}
          </div>
        </div>
        <div className="event-content">
          {renderEventContent(type, payload)}
        </div>
      </div>
    );

    return eventCard;
  };

  const renderEventContent = (type, payload) => {
    switch (type) {
      case 'workflow_start':
        return (
          <div className="event-payload">
            <p className="payload-text">{payload}</p>
          </div>
        );
      case 'main_task_result':
        return (
          <div className="event-payload">
            <p className="payload-text"><strong>{payload.agent}:</strong> {payload.result}</p>
          </div>
        );
      case 'coordination_start':
        return (
          <div className="event-payload">
            <p className="payload-text">{payload}</p>
          </div>
        );
      case 'coordination_result':
        return (
          <div className="event-payload">
            <p className="payload-text"><strong>:</strong> {payload.total_agents}</p>
            <div className="responses-summary">
              {payload.responses.map((response, index) => (
                <div key={index} className="response-summary">
                  <span className="response-agent">{response.agent}:</span>
                  <span className="response-text">{response.result.substring(0, 100)}...</span>
                </div>
              ))}
            </div>
          </div>
        );
      case 'workflow_complete':
        return (
          <div className="event-payload">
            <p className="payload-text">{payload}</p>
          </div>
        );
      case 'agent_message':
        return (
          <div className="event-payload">
            <p className="payload-text">{payload}</p>
          </div>
        );
      case 'agent_response':
        return (
          <div className="event-payload">
            <p className="payload-text"><strong>{payload.agent}:</strong> {payload.result}</p>
          </div>
        );
      case 'analysis_report':
        return (
          <div className="event-payload">
            <div className="report-content">
              <h4 className="report-title">  / City Analysis Report</h4>
              <div className="report-body">
                {payload && payload.report ? (
                  <div className="report-text">
                    {payload.report.split('\n').map((line, index) => (
                      <p key={index} className="report-paragraph">{line}</p>
                    ))}
                  </div>
                ) : (
                  <p className="report-text">... / Report content loading...</p>
                )}
              </div>
            </div>
          </div>
        );
      case 'traffic_incident':
        return (
          <div className="event-payload">
            <p className="payload-text"><strong>:</strong> {payload.location} - {payload.description}</p>
          </div>
        );
      case 'weather_alert':
        return (
          <div className="event-payload">
            <p className="payload-text"><strong>:</strong> {payload.area} - <strong>:</strong> {payload.alert_type} - <strong>:</strong> {payload.severity}</p>
          </div>
        );
      case 'parking_update':
        return (
          <div className="event-payload">
            <p className="payload-text"><strong>:</strong> {payload.location} - <strong>:</strong> {payload.available_spots}</p>
          </div>
        );
      case 'safety_inspection':
        return (
          <div className="event-payload">
            <p className="payload-text"><strong>:</strong> {payload.location} - <strong>:</strong> {payload.safety_status} - <strong>:</strong> {payload.require_human_intervention ? '' : ''}</p>
          </div>
        );
      case 'autonomous_driving':
        return (
          <div className="event-payload">
            <p className="payload-text"><strong>:</strong> {payload.start_location} - <strong>:</strong> {payload.end_location} - <strong>:</strong> {payload.passengers}</p>
          </div>
        );
      case 'traffic_monitor':
        return (
          <div className="event-payload">
            <p className="payload-text"><strong>:</strong> {JSON.stringify(payload)}</p>
          </div>
        );
      case 'simulation_log':
        return (
          <div className="event-payload">
            <p className="payload-text">{payload.details || payload}</p>
          </div>
        );
      case 'error':
        return (
          <div className="event-payload">
            <p className="payload-error">{payload}</p>
          </div>
        );
      case 'ping':
        return null; // Don't display ping messages
      default:
        return (
          <div className="event-payload">
            <p className="payload-text">{JSON.stringify(payload)}</p>
          </div>
        );
    }
  };

  const viewEventDetails = (event) => {
    alert(` / Event Details\n\n / Type: ${getEventTypeLabel(event.type)}\n / Time: ${event.timestamp}\n\n / Detailed information will be shown in a modal.`);
  };

  const replayEvent = (event) => {
    if (confirm(` / Are you sure you want to replay this event?`)) {
      // 
      alert(' / Event replayed successfully!');
    }
  };

  const retryEvent = (event) => {
    if (confirm(` / Are you sure you want to retry this event?`)) {
      // 
      alert(' / Event retried successfully!');
    }
  };

  const getEventStats = () => {
    const total = filteredEvents.length;
    const byType = filteredEvents.reduce((acc, event) => {
      acc[event.type] = (acc[event.type] || 0) + 1;
      return acc;
    }, {});
    return { total, byType };
  };

  // 
  const getSentEvents = () => {
    const sentEventTypes = [
      'weather_alert', 
      'parking_update', 
      'safety_inspection', 
      'autonomous_driving', 
      'traffic_monitor',
      'broadcast',
      'main_coordination',
      'sub_agent_coordination',
      'sub_agent_processing',
      'sub_agent_completed',
      'result_summary',
      'coordination_result',
      'analysis_report'
    ];
    return events.filter(event => sentEventTypes.includes(event.type));
  };

  // 
  const getSentEventsCount = () => {
    return getSentEvents().length;
  };

  // 
  const getSentEventSummary = (event) => {
    if (!event) {
      return '';
    }
    
    // titletitle
    if (event.title) {
      return event.title;
    }
    
    // payload
    if (event.payload) {
      switch (event.type) {
        case 'weather_alert':
          return `: ${event.payload.area || ''} - : ${event.payload.alert_type || ''}`;
        case 'parking_update':
          return `: ${event.payload.location || ''} - : ${event.payload.available_spots || ''}`;
        case 'safety_inspection':
          return `: ${event.payload.location || ''} - : ${event.payload.safety_status || ''}`;
        case 'autonomous_driving':
          return `: ${event.payload.start_location || ''} - : ${event.payload.end_location || ''}`;
        case 'traffic_monitor':
          return ``;
        case 'broadcast':
          return event.payload.payload || event.payload.message || '';
        case 'main_coordination':
          return event.payload.result || '';
        case 'sub_agent_coordination':
          return event.payload || '';
        case 'sub_agent_processing':
          return event.payload.agent || '';
        case 'sub_agent_completed':
          return event.payload.result || '';
        case 'result_summary':
          return `: ${event.payload.total_agents || 0} , : ${event.payload.successful_agents || 0} `;
        case 'coordination_result':
          return event.payload.summary || '';
        case 'analysis_report':
          return event.payload.report ? event.payload.report.substring(0, 100) + '...' : '';
        default:
          return event.type || '';
      }
    }
    
    return event.type || '';
  };

  // 
  const getRelatedEvents = (sentEvent) => {
    if (!sentEvent || !sentEvent.timestamp) {
      return [];
    }
    
    const sentTime = new Date(sentEvent.timestamp);
    const timeWindow = 10 * 60 * 1000; // 10
    
    // 
    const responseEventTypes = [
      'workflow_start',
      'main_task_result', 
      'coordination_start',
      'coordination_result',
      'workflow_complete',
      'agent_message',
      'agent_response',
      'analysis_report',
      'simulation_log'
    ];
    
    return events.filter(event => {
      if (!event || !event.timestamp) return false;
      
      const eventTime = new Date(event.timestamp);
      const timeDiff = Math.abs(eventTime.getTime() - sentTime.getTime());
      
      // 
      return timeDiff <= timeWindow && 
             responseEventTypes.includes(event.type) && 
             event.type !== sentEvent.type;
    }).sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp));
  };

  //  - 
  const getFilteredEvents = () => {
    if (showReportsOnly) {
      return events.filter(event => event.type === 'analysis_report');
    }
    return events;
  };

  const filteredEvents = getFilteredEvents();
  const stats = getEventStats();

  return (
    <div className="interaction-record card">
      <div className="history-header">
        <h2 className="card__title">  / Interaction History</h2>
        <div className="header-controls">
          {getStatusPill()}
          {connectionStats && (
            <span className="pill pill-info">
               {connectionStats.userId ? connectionStats.userId.substring(0, 8) + '...' : 'Unknown'}
            </span>
          )}
        </div>
        {connectionError && (
          <div className="connection-error-message">
            <span className="error-icon"></span>
            <span className="error-text">{connectionError}</span>
          </div>
        )}
      </div>
      
      {/*  */}
      <div className="connection-info">
        <div className="connection-details">
          <span className="connection-item">
            <strong> / Messages:</strong> {events.length}
          </span>
          <span className="connection-item">
            <strong> / Backend:</strong> http://localhost:8008
          </span>
          {connectionStats && (
            <>
              <span className="connection-item">
                <strong>ID / User ID:</strong> {connectionStats.userId}
              </span>
              <span className="connection-item">
                <strong> / Active Connections:</strong> {connectionStats.activeConnections}/{connectionStats.totalConnections}
              </span>
            </>
          )}
        </div>
      </div>

      <div className="history-controls">
        <div className="action-buttons">
          <button onClick={onOpenReportSidebar} className="report-sidebar-btn">
              / Interaction Report
          </button>
          <button onClick={onGenerateReport} className="generate-btn">
              / Generate Report
          </button>
          <button onClick={onClear} className="clear-btn">
              / Clear History
          </button>
          <button 
            onClick={onToggleReportsOnly} 
            className={`view-reports-btn ${showReportsOnly ? 'active' : ''}`}
          >
            {showReportsOnly ? '  / Show All' : '  / View Reports'}
          </button>
        </div>
      </div>

      <div className="history-layout">
        {/*  */}
        <div className="sent-events-sidebar">
          <div className="sidebar-header">
            <h3>{showReportsOnly ? '  / Report Info' : '  / Sent Events'}</h3>
            <span className="event-count">
              {showReportsOnly ? `${filteredEvents.length} ` : `${getSentEventsCount()} `}
            </span>
          </div>
          <div className="sent-events-list">
            {showReportsOnly ? (
              // 
              filteredEvents.length === 0 ? (
                <div className="empty-state">
                  <div className="empty-icon"></div>
                  <p className="empty-text"> / No reports generated yet</p>
                  <p className="empty-subtext">"" / Click "Generate Report" to create reports</p>
                </div>
              ) : (
                filteredEvents.map((report, index) => (
                  <div key={index} className="report-summary-item">
                    <div className="event-header">
                      <span className="event-icon"></span>
                      <div className="event-info">
                        <div className="event-title">{report.title || ''}</div>
                        <div className="event-meta">
                          <span className="event-time">{report.timestamp ? new Date(report.timestamp).toLocaleTimeString() : ''}</span>
                        </div>
                      </div>
                    </div>
                    <div className="event-summary">
                      {report.payload && report.payload.report ? 
                        `: ${report.payload.report.substring(0, 100)}...` : 
                        '...'
                      }
                    </div>
                  </div>
                ))
              )
            ) : (
              // 
              getSentEvents().length === 0 ? (
                <div className="empty-state">
                  <div className="empty-icon"></div>
                  <p className="empty-text"> / No sent events yet</p>
                  <p className="empty-subtext"> / Send tasks to view event details</p>
                </div>
              ) : (
                getSentEvents().map((event, index) => (
                  <div 
                    key={index} 
                    className={`sent-event-item ${selectedSentEvent === index ? 'selected' : ''}`}
                    onClick={() => setSelectedSentEvent(index)}
                  >
                    <div className="event-header">
                      <span className="event-icon">{getEventIcon(event.type)}</span>
                      <div className="event-info">
                        <div className="event-title">{getEventTypeLabel(event.type)}</div>
                        <div className="event-meta">
                          <span className="event-time">{event.timestamp ? new Date(event.timestamp).toLocaleTimeString() : ''}</span>
                        </div>
                      </div>
                    </div>
                    <div className="event-summary">
                      {getSentEventSummary(event)}
                    </div>
                  </div>
                ))
              )
            )}
          </div>
        </div>

        {/*  */}
        <div className="interactions-detail">
          {selectedSentEvent !== null ? (
            <div className="detail-view">
              <div className="detail-header">
                <h3>  / Agent Interaction Records</h3>
                <button 
                  className="close-detail"
                  onClick={() => setSelectedSentEvent(null)}
                >
                  
                </button>
              </div>
              <div className="detail-content">
                {(() => {
                  const sentEvent = getSentEvents()[selectedSentEvent];
                  if (!sentEvent) return null;
                  
                  // 
                  const relatedEvents = getRelatedEvents(sentEvent);
                  
                  return (
                    <div className="interaction-flow">
                      {/*  */}
                      <div className="sent-event-detail">
                        <div className="detail-section-header">
                          <span className="section-icon"></span>
                          <span className="section-title"> / Sent Event Details</span>
                        </div>
                        <div className="event-detail-content">
                          <div className="detail-item">
                            <strong> / Event Type:</strong> {getEventTypeLabel(sentEvent.type)}
                          </div>
                          <div className="detail-item">
                            <strong> / Sent Time:</strong> {new Date(sentEvent.timestamp).toLocaleString()}
                          </div>
                          <div className="detail-item">
                            <strong> / Event Details:</strong>
                            <div className="event-payload-detail">
                              {renderEventContent(sentEvent.type, sentEvent.payload)}
                            </div>
                          </div>
                        </div>
                      </div>

                      {/*  */}
                      <div className="agent-visualization">
                        <div className="detail-section-header">
                          <span className="section-icon"></span>
                          <span className="section-title"> / Agent Interaction Visualization</span>
                        </div>
                        <div className="visualization-container">
                          <AgentInteractionFlow 
                            events={events} 
                            selectedEvent={sentEvent}
                          />
                        </div>
                      </div>

                      {/*  */}
                      <div className="agent-responses">
                        <div className="detail-section-header">
                          <span className="section-icon"></span>
                          <span className="section-title"> / Agent Response Flow</span>
                        </div>
                        <div className="response-timeline">
                          {relatedEvents.length === 0 ? (
                            <div className="no-responses">
                              <div className="no-responses-icon"></div>
                              <p> / Waiting for agent responses...</p>
                            </div>
                          ) : (
                            relatedEvents.map((event, index) => (
                              <div key={index} className="response-item">
                                <div className="response-header">
                                  <span className="response-icon">{getEventIcon(event.type)}</span>
                                  <span className="response-type">{getEventTypeLabel(event.type)}</span>
                                  <span className="response-time">{new Date(event.timestamp).toLocaleTimeString()}</span>
                                  <button 
                                    className={`expand-btn ${expandedEvents.has(`response_${selectedSentEvent}_${index}`) ? 'expanded' : ''}`}
                                    onClick={() => {
                                      const eventKey = `response_${selectedSentEvent}_${index}`;
                                      const newExpanded = new Set(expandedEvents);
                                      if (newExpanded.has(eventKey)) {
                                        newExpanded.delete(eventKey);
                                      } else {
                                        newExpanded.add(eventKey);
                                      }
                                      setExpandedEvents(newExpanded);
                                    }}
                                  >
                                    {expandedEvents.has(`response_${selectedSentEvent}_${index}`) ? '' : ''}
                                  </button>
                                </div>
                                
                                {/*  */}
                                <div className="response-summary">
                                  {getEventSummary(event)}
                                </div>
                                
                                {/*  */}
                                {expandedEvents.has(`response_${selectedSentEvent}_${index}`) && (
                                  <div className="response-details">
                                    {renderEventContent(event.type, event.payload)}
                                  </div>
                                )}
                              </div>
                            ))
                          )}
                        </div>
                      </div>
                    </div>
                  );
                })()}
              </div>
            </div>
          ) : (
            <div className="detail-placeholder">
              <div className="placeholder-icon"></div>
              <p className="placeholder-text"></p>
              <p className="placeholder-subtext">Click on a sent event to view agent interaction records</p>
            </div>
          )}
        </div>
      </div>

      <div className="history-footer">
        <div className="footer-stats">
          <span className="stat-item">
            <span className="stat-label"> / Total Events:</span>
            <span className="stat-value">{stats.total}</span>
          </span>
          <span className="stat-item">
            <span className="stat-label"> / Showing:</span>
            <span className="stat-value">{filteredEvents.length}</span>
          </span>
        </div>
      </div>
    </div>
  );
};

export default InteractionHistory;