// frontend/src/components/SimpleWebSocketService.js
import { io } from 'socket.io-client';

class SimpleWebSocketService {
  constructor() {
    this.socket = null;
    this.isConnected = false;
    this.onConnectCallback = null;
    this.onDisconnectCallback = null;
    this.onMessageCallback = null;
    this.onErrorCallback = null;
    this.userId = this.generateUserId();
    this.lastError = null;
    this.connectionStats = {
      totalConnections: 0,
      successfulConnections: 0,
      failedConnections: 0,
      lastConnectionTime: null,
      lastError: null
    };
  }

  generateUserId() {
    if (typeof window === 'undefined') {
      return `user_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
    }
    
    let userId = localStorage.getItem('websocket_user_id');
    if (!userId) {
      userId = `user_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
      localStorage.setItem('websocket_user_id', userId);
    }
    return userId;
  }

  connect(url) {
    console.log(' :', url);
    
    // 
    if (this.socket) {
      this.socket.disconnect();
      this.socket = null;
    }
    
    try {
      // URL
      const baseUrl = url.replace(/\/$/, ''); // 
      const connectionUrl = `${baseUrl}?user_id=${this.userId}`;
      
      // Socket.IO
      this.socket = io(connectionUrl, {
        transports: ['websocket', 'polling'],
        timeout: 30000,
        forceNew: true,
        reconnection: true,
        reconnectionAttempts: 10,
        reconnectionDelay: 2000,
        reconnectionDelayMax: 10000,
        maxReconnectionAttempts: 10,
        autoConnect: true,
        upgrade: true,
        rememberUpgrade: true
      });

      this.socket.on('connect', () => {
        console.log(' WebSocket');
        this.isConnected = true;
        this.lastError = null;
        this.connectionStats.successfulConnections++;
        this.connectionStats.lastConnectionTime = new Date().toISOString();
        if (this.onConnectCallback) {
          this.onConnectCallback();
        }
      });

      this.socket.on('disconnect', (reason) => {
        console.log(' WebSocket:', reason);
        this.isConnected = false;
        if (this.onDisconnectCallback) {
          this.onDisconnectCallback(reason);
        }
      });

      this.socket.on('connect_error', (error) => {
        console.error(' WebSocket:', error);
        this.isConnected = false;
        this.lastError = error;
        this.connectionStats.failedConnections++;
        this.connectionStats.lastError = error.message || error.toString();
        if (this.onErrorCallback) {
          this.onErrorCallback(error);
        }
      });

      this.socket.on('reconnect', (attemptNumber) => {
        console.log(' WebSocket:', attemptNumber);
        this.isConnected = true;
        if (this.onConnectCallback) {
          this.onConnectCallback();
        }
      });

      this.socket.on('reconnect_error', (error) => {
        console.error(' WebSocket:', error);
        this.isConnected = false;
      });

      // 
      this.socket.on('connection_successful', (data) => {
        console.log(' :', data);
        this.isConnected = true;
        if (this.onConnectCallback) {
          this.onConnectCallback();
        }
      });

      // 
      this.socket.on('server_stats', (stats) => {
        console.log(' :', stats);
      });

      // 
      this.socket.onAny((eventName, ...args) => {
        console.log(' :', eventName, args);
        if (this.onMessageCallback) {
          const message = args[0] || {};
          this.onMessageCallback(message);
        }
      });

      // 
      this.socket.on('broadcast', (data) => {
        console.log(' :', data);
        if (this.onMessageCallback) {
          this.onMessageCallback(data);
        }
      });

    } catch (error) {
      console.error(' WebSocket:', error);
      if (this.onErrorCallback) {
        this.onErrorCallback(error);
      }
    }
  }

  send(event, data) {
    if (!this.isConnected || !this.socket) {
      console.error(' WebSocket');
      return;
    }

    console.log(' :', { event, data });
    
    try {
      this.socket.emit(event, data);
    } catch (error) {
      console.error(' :', error);
      if (this.onErrorCallback) {
        this.onErrorCallback(error);
      }
    }
  }

  on(event, callback) {
    switch (event) {
      case 'connect':
        this.onConnectCallback = callback;
        break;
      case 'disconnect':
        this.onDisconnectCallback = callback;
        break;
      case 'message':
        this.onMessageCallback = callback;
        break;
      case 'error':
        this.onErrorCallback = callback;
        break;
      default:
        console.error(`Unknown event: ${event}`);
    }
  }

  disconnect() {
    this.isConnected = false;
    if (this.socket) {
      this.socket.disconnect();
      this.socket = null;
    }
    if (this.onDisconnectCallback) {
      this.onDisconnectCallback();
    }
  }

  reconnect() {
    console.log(' WebSocket...');
    if (this.socket) {
      this.socket.disconnect();
    }
    // 
    setTimeout(() => {
      this.connect('http://localhost:8008');
    }, 1000);
  }

  getConnectionStats() {
    return {
      ...this.connectionStats,
      connected: this.isConnected,
      userId: this.userId,
      socketId: this.socket?.id || null,
      lastError: this.lastError
    };
  }

  getConnectionStatus() {
    if (!this.socket) {
      return { status: 'disconnected', message: '' };
    }
    
    if (this.isConnected) {
      return { status: 'connected', message: '' };
    }
    
    if (this.lastError) {
      return { 
        status: 'error', 
        message: `: ${this.lastError.message || this.lastError}` 
      };
    }
    
    return { status: 'connecting', message: '...' };
  }

  // 
  reconnect() {
    if (this.socket) {
      console.log(' WebSocket');
      this.socket.disconnect();
      this.socket.connect();
    }
  }
}

const instance = new SimpleWebSocketService();
export default instance;
