import React from 'react';
import './TaskInput.css';

const TaskInput = ({ 
  sendMessage, 
  setSendMessage, 
  onExecuteTask, 
  isMultiAgentMode,
  selectedAgents,
  isLoading 
}) => {
  const taskTemplates = [
    " ",
    " ",
    " ", 
    " ",
    " ",
    " "
  ];

  const handleTemplateClick = (template) => {
    setSendMessage(template);
  };

  const handleExecute = () => {
    if (isMultiAgentMode) {
      if (selectedAgents.length < 2) {
        alert('2');
        return;
      }
    } else {
      if (selectedAgents.length !== 1) {
        alert('1');
        return;
      }
    }
    
    if (!sendMessage.trim()) {
      alert('');
      return;
    }

    onExecuteTask();
  };

  const isExecuteDisabled = () => {
    if (isLoading) return true;
    if (!sendMessage.trim()) return true;
    if (isMultiAgentMode) {
      return selectedAgents.length < 2;
    } else {
      return selectedAgents.length !== 1;
    }
  };

  return (
    <div className="task-input">
      <div className="input-header">
        <h3 className="input-title"> / Task Planning</h3>
        <div className="input-subtitle"></div>
      </div>

      <div className="input-area">
        <textarea
          className="task-textarea"
          placeholder="..."
          value={sendMessage}
          onChange={(e) => setSendMessage(e.target.value)}
          rows="4"
          disabled={isLoading}
        />
        
        <div className="template-section">
          <h4 className="template-title"></h4>
          <div className="template-grid">
            {taskTemplates.map((template, index) => (
              <button
                key={index}
                className="template-btn"
                onClick={() => handleTemplateClick(template)}
                disabled={isLoading}
              >
                {template}
              </button>
            ))}
          </div>
        </div>

        <div className="execute-section">
          <button 
            className={`execute-btn ${isExecuteDisabled() ? 'disabled' : ''}`}
            onClick={handleExecute}
            disabled={isExecuteDisabled()}
          >
            {isLoading ? (
              <>
                <span className="loading-spinner"></span>
                ...
              </>
            ) : (
              <>
                <span className="execute-icon"></span>
                {isMultiAgentMode ? '' : ''}
              </>
            )}
          </button>
          
          <div className="execute-info">
            <div className="info-item">
              <span className="info-label"></span>
              <span className="info-value">{isMultiAgentMode ? '' : ''}</span>
            </div>
            <div className="info-item">
              <span className="info-label"></span>
              <span className="info-value">{selectedAgents.length} </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TaskInput;
